import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';

const classes = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  input: {
    margin: theme.spacing.unit,
  },
});

class App extends Component {
  state = {
    button: "Hello Wordl!",
    inputOne: "Tex field One",
    inputTwo: "Input Two"
  }
  onHandlerChange = (e) =>{
    this.setState({
      [e.target.name]: e.target.value
    });
  }
  onHandlerButtonClick = (e) =>{
    e.preventDefault();
    console.log(this.state);
  }
  render() {
    return (
      <div className={classes.container}>
        <Input
          defaultValue={this.state.inputOne}
          className={classes.input}
          inputProps={{
            'aria-label': 'Description',
          }}
          name="inputOne"
          onChange={this.onHandlerChange.bind(this)}
        />
        <br />
        <br />
        <Input
          defaultValue={this.state.inputTwo}
          className={classes.input}
          inputProps={{
            'aria-label': 'Description',
          }}
          name="inputTwo"
          onChange={this.onHandlerChange.bind(this)}
        />        
        <br />
        <br />
        <Button variant="contained" color="primary" onClick={this.onHandlerButtonClick} >
          {this.state.button}
        </Button>
      </div>
    );
  }
}

export default App;



